@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@400;700&display=swap);
:root{--color-black: #000;--color-white: #fff;--color-light-black: #161616;--color-light-grey: #d3d3d3;--color-dark-grey: #9f9f9f;--color-darkest-grey: #8c8c8c;--color-lime-green: #cbd736;--background-color: #1e1e1e;--background-effect: linear-gradient(
    180deg,
    var(--background-color) 0%,
    var(--background-color) 100%
  );--background-color-card: #000000cc;--background-color-card-img: #1b1b1b;--modal-background-color: #00000080;--border-color: #606060;--loading-transform: matrix(-1, 0, 0, 1, 0, 0);--radius-medium: 8px;--radius-large: 16px;--shadow: 8px 8px 64px #00000066, 16px 16px 32px #0000004d;--filter: drop-shadow(0 4px 79px var(--color-black))}

*,*::before,*::after{box-sizing:border-box}body{margin:0;font-family:'Nunito', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

